<template>
  <div class="banners">
     <swiper :options="swiperOption" ref="mySwiper" @someSwiperEvent="callback">
        <swiper-slide>
          <div class="swiper-container banner">
            <div class="img-banner">
              <img class="img_" src="https://cdn.youlianyc.com/image/static/e37fae54b95f0483ef49dbc3d85a609f6d808f6f.jpg" />
              <button @click="slideTo" class="btn_join_">免费入驻</button>
            </div>
          </div>
       </swiper-slide>
       <swiper-slide>
          <div class="swiper-container banner">
            <div class="img-banner">
              <img class="img_ b_" src="https://cdn.youlianyc.com/image/static/044b9bc6684fbe5ff07c677ea12acf358871533b.jpg" />
            </div>
            <div class="login-block">
              <div class="title">免费入驻</div>
              <form action="" class="form-block">
                <div class="info-area">
                  <div class="cdk-input">
                    <span class="icon-span icon-mobile"></span>
                    <input type="tel" class="input-t" v-model="mobile" placeholder="输入手机号码" />
                  </div>
                </div>
                <div class="info-area">
                  <div class="cdk-input cdk-input-short">
                    <span class="icon-span icon-imgQR"></span>
                    <input type="text" class="input-t" v-model="phrase" placeholder="请输入右侧验证码" />
                  </div>
                  <img class="imgQR" :src="phrase_url"/>
                  <div @click="changePhrasePath" class="change">看不清换一张</div>
                </div>
                <div class="info-area">
                  <div class="cdk-input cdk-input-short">
                    <span class="icon-span icon-msgQR"></span>
                    <input type="text" class="input-t" v-model="code" placeholder="请输入短信验证码" />
                  </div>
                  <div @click="cdkSendSms" :class="{'gray_': countDown > 0}" class="sendQR">{{countDown ? countDown : '发送验证码'}}</div>
                </div>
                <div class="info-area">
                  <div class="cdk-input">
                    <span class="icon-span icon-inviteQR"></span>
                    <input type="text" class="input-t" v-model="invite_code" placeholder="请输入邀请码（必填）" />
                  </div>
                </div>
                <div style="color: red;">{{error}}</div>
                <div @click="submit" class="btn-area">免费入驻</div>
                <div class="clearfix">
                  <div @click="jumpToLogin" class="a_join_" to="/">已有账号？去登录</div>
                </div>
              </form>
            </div>
          </div>
       </swiper-slide>
       <div class="swiper-pagination" slot="pagination"></div>
     </swiper>
  </div>
</template>

<script type="text/ecmascript-6">
import 'swiper/dist/css/swiper.css'
 
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import {mapState, mapMutations} from 'vuex'
import api from '@/utils/api'

export default {
  components: {
    swiper,
    swiperSlide
  },
  name: 'carrousel',
  data() {
    return {
      swiperOption: {
        allowTouchMove: false,
        grabCursor: false,
        // loop : true,
      },
      mobile: '',
      phrase: '',
      code: '',
      invite_code: '',
      countDown: 0,
      error: '',
    }
  },
  watch: {
    'jumpToJoin':  function (val, oldval) {
      console.log(this, 'watch', val, oldval)
      if (val == 1) {
        this.swiper.slideTo(1, 1000, false);
      } else if(val == 3) {
        this.swiper.slideTo(0, 1000, false);
      }
    }
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper
    },
    ...mapState('pc', [
      'jumpToJoin'
    ]),
    ...mapState('account', [
      '_version',
      'phrase_url'
    ])
  },
  mounted() {
    this.changePhrasePath()
    console.log(this.jumpToJoin, '1', this.$route.query)
    // this.$router.push('/Register')
    if (this.$route.query.jumpToJoin) {
      this.slideTo()
    }
    let invite_id = this.$route.query.invite || window.localStorage.getItem('invite_id')
    if (invite_id) {
      window.localStorage.setItem('invite_id', invite_id)
      this.invite_code = invite_id
    }
    console.log('invite_code -> ', this.invite_code)
  },
  methods: {
    ...mapMutations('pc', [
      'updateJumpToJoin'
    ]),
    ...mapMutations('account', [
      'changePhrasePath'
    ]),
    callback () {
    },
    jumpToLogin () {
      console.log(this.$router)
      this.$router.push('/login')
    },
    submit () {
      let bd_id = this.$route.query.bd_id || ''
      api.cdkRegister({
        mobile: this.mobile,
        phrase: this.phrase,
        code: this.code,
        invite_code: this.invite_code,
        _version: this._version,
        bd_id
      }).then(res => res.data).then(data => {
        console.log('cdkRegister res: ', data)
        if (data.status === 'ok') {
          let path = `/Register?id=${data.id}`
          if (bd_id) {
            path += `&bd_id=${bd_id}`
          }
          this.$router.push(path)
        } else {
          this.error = data.message
        }
      })
    },
    cdkSendSms () {
      if (this.countDown > 0 || !this.mobile || !/^1[3-9][0-9][\d]{8}$/.test(this.mobile)) {
        return
      }
      api.cdkSendSms({
        mobile: this.mobile
      }).then(res => res.data).then(data => {
        if (data.code === 200) {
          this.startCountDown()
        }
      })
    },
    startCountDown () {
      this.countDown = 60
      let si = setInterval(() => {
        this.countDown--
        if (this.countDown < 1) {
          clearInterval(si)
        }
      }, 1000);
    },
    slideTo () {
      console.log(this.swiper)
      this.updateJumpToJoin(1)
      this.swiper.slideTo(1, 1000, false);
    }
  }
}
</script>

