<template>
  <div class="home">
    <!--<img alt="Vue logo" src="../assets/logo.png">-->
    <div class="banner-bg">
      <Tab></Tab>
      <banner></banner>
    </div>
    <homePage></homePage>
    <div class="check_form_">
      <div class="title_">资深运营1对1诊断，免费制定营销方案</div>
      <div class="desc_">轻松获取流量，快速超越同行</div>
      <div class="wrap_">
        <div class="item_">店铺链接：<input v-model="formInfo.shop_url" class="input_" placeholder="请输入您在拼多多的店铺地址"></div>
        <div class="item_">您的店铺名：<input v-model="formInfo.shop_name" class="input_" placeholder="请输入您在拼多多的店铺名称"></div>
      </div>
      <div class="wrap_">
        <div class="item_">您的手机号：<input v-model="formInfo.mobile" class="input_" placeholder="请输入您手机号码"></div>
        <div class="item_">手机验证码：<input v-model="formInfo.code" class="input_" placeholder="请输入手机验证码">
        <button @click="cdkSendSms" class="btn_get_code_" :class="{'gray_' : countDown > 0}">{{countDown > 0 ? countDown : '获取验证码'}}</button></div>
      </div>
      <div class="error_">{{error}}</div>
      <div><button @click="submitCheck" class="btn_submit_">点击获取诊断结果</button></div>
    </div>
    <div class="popup_" v-if="successPopup">
      <div class="mask_" @click="closePop()"></div>
      <div class="con_">
        <div class="title_">
          <div class="a_">【
            <span>{{formInfo.shop_name}}</span> 】</div>
          <div class="b_">提交成功，稍后我们会电话联系您</div>
          <!-- <div class="flex_">
            <img v-for="item in bd_qrcodes" :src="item" :key="item">
          </div> -->
        </div>
      </div>
    </div>
    <footer-bar></footer-bar>
  </div>
</template>

<script>
// @ is an alias to /src
import homePage from '@/components/homePage.vue'
import Tab from '@/components/tab.vue'
import Banner from '@/components/banner.vue'
import footerBar from '@/components/footerBar'
import api from '@/utils/api'

export default {
  name: 'home',
  components: {
    homePage,
    Tab,
    Banner,
    footerBar
  },
  data () {
    return {
      error: '',
      countDown: 0,
      successPopup: false,
      formInfo: {
        shop_url: '',
        shop_name: '',
        mobile: '',
        code: ''
      }
    }
  },
  mounted () {
    document.title = '超多客首页'
    if (this.$route.query.bd_id) {
      window.localStorage.setItem('bd_id', this.$route.query.bd_id || '')
    }
  },
  methods: {
    closePop () {
      this.successPopup = false
      // location.href = '/'
    },
    cdkSendSms () {
      if (this.countDown > 0 || !this.formInfo.mobile || !/^1[3-9][0-9][\d]{8}$/.test(this.formInfo.mobile)) {
        return
      }
      this.error = ''
      api.cdkSendSms({
        mobile: this.formInfo.mobile
      }).then(res => res.data).then(data => {
        if (data.code === 200) {
          this.startCountDown()
        } else {
          this.error = data.message
        }
      })
    },
    startCountDown () {
      this.countDown = 60
      let si = setInterval(() => {
        this.countDown--
        if (this.countDown < 1) {
          clearInterval(si)
        }
      }, 1000);
    },
    submitCheck () {
      this.error = ''
      api.cdkPostBaseData(this.formInfo).then(res => res.data).then(data => {
        if (data.code === 200) {
          this.successPopup = true
        } else {
          this.error = data.message
        }
      })
    }
  }
}
</script>
