export default {
    namespaced: true,
    state : {
        phrase_url: '',
        _version: ''
    },
    mutations : {
        changePhrasePath (state) {
            state._version = Date.now() + Math.random() * 10000
            state.phrase_url = '/phrase?_version=' + state._version
        },
        setData(state, payload) {
            state.test  = payload
        }
    },
    actions : {
        fetchData ({commit}, params) {
            console.log(commit, params)
        }
    }
}