import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/huan-web',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    },
    {
      path: '/qrcode/:id', name: 'qrcode',
        component: () => import('./views/Qrcode.vue')
    },
    {
      path: '/weixin_qrcode', name: 'weixin_qrcode',
        component: () => import('./views/WeixinQrcode.vue')
    },
    {
      path: '/register', name: 'register',
        component: () => import('./views/Register.vue')
    },
    {
      path: '/login', name: 'login',
        component: () => import('./views/Login.vue')
    },
    {
      path: '/toMiniProgram', name: 'toMiniProgram',
      meta: { title: '精品推荐', page_name: 'toMiniProgram'},
        component: () => import('./views/toMiniProgram.vue')
    },
    {
      path: '/toMiniProgramNew', name: 'toMiniProgramNew',
      meta: { title: '精品推荐', page_name: 'toMiniProgramNew'},
        component: () => import('./views/toMiniProgramNew.vue')
    },
  ]
})
